import React, { useState } from 'react';
import classNames from 'classnames';
import { Movie } from '../../types/Movie';
import { getMovie } from '../../api';
import './FindMovie.scss';
import { MovieCard } from '../MovieCard';

type Props = {
  movies: Movie[],
  handleMovie: (movie: Movie) => void;
};

export const FindMovie: React.FC<Props> = ({ movies, handleMovie }) => {
  const [movie, setMovie] = useState<Movie | null>(null);
  const [query, setQuery] = useState('');
  const [isFetching, setIsFetching] = useState(false);
  const [isSearched, setIsSearched] = useState(false);

  const handleFindMovie = async () => {
    setIsFetching(true);

    try {
      const foundMovie = await getMovie(query);

      if ('Error' in foundMovie) {
        setMovie(null);
      } else {
        const newMovie = {
          title: foundMovie.Title,
          description: foundMovie.Plot,
          imgUrl: foundMovie.Poster,
          imdbUrl: `https://www.imdb.com/title/${foundMovie.imdbID}/`,
          imdbId: foundMovie.imdbID,
        };

        setMovie(newMovie);
      }
    } finally {
      setIsFetching(false);
      setIsSearched(true);
    }
  };

  const handleAddMovie = () => {
    const copy = movies.find(prev => prev.title === movie?.title);

    if (!movie || copy) {
      return;
    }

    handleMovie(movie);

    setIsSearched(false);
    setMovie(null);
    setQuery('');
  };

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setQuery(event.target.value);
    setIsSearched(false);
  };

  return (
    <>
      <form className="find-movie">
        <div className="field">
          <label className="label" htmlFor="movie-title">
            Movie title
          </label>

          <div className="control">
            <input
              data-cy="titleField"
              type="text"
              id="movie-title"
              placeholder="Enter a title to search"
              className={classNames(
                'input',
                {
                  'is-danger': isSearched && !movie,
                },
              )}
              value={query}
              onChange={handleInputChange}
            />
          </div>

          {(!movie && isSearched) && (
            <p className="help is-danger" data-cy="errorMessage">
              Can&apos;t find a movie with such a title
            </p>
          )}
        </div>

        <div className="field is-grouped">
          <div className="control">
            <button
              data-cy="searchButton"
              type="submit"
              className={classNames(
                'button is-light',
                { 'is-loading': isFetching },
              )}
              disabled={!query.trim() || (isSearched && !movie) || isFetching}
              onClick={handleFindMovie}
            >
              {!movie ? 'Find a movie' : 'Search again'}
            </button>
          </div>

          {movie && (
            <div className="control">
              <button
                data-cy="addButton"
                type="button"
                className="button is-primary"
                onClick={handleAddMovie}
              >
                Add to the list
              </button>
            </div>
          )}
        </div>
      </form>

      {movie && (
        <div className="container" data-cy="previewContainer">
          <h2 className="title">Preview</h2>
          <MovieCard movie={movie} />
        </div>
      )}
    </>
  );
};
